<template>
	<div>
		<template v-if="loadingCategory">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__primary">
						<h3 class="user-profile__name">Category Detail Section</h3>
					</div>
					<div class="user-profile__item">
						<h5 class="user-profile__section__title">Category Name:</h5>
						<p>{{ itemData.name }}</p>
					</div>
					<div v-if="getLoggedUser.category === 'administrator'">
						<button @click="deleteCategory" class="btn btn__danger">Delete</button>
						<Loader v-if="deletingCategory" />
					</div>
				</div>

				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="activity-tab" data-toggle="tab" href="#activity" role="tab" aria-controls="activity" aria-selected="false">Edit Category</a>
						</li>
					</ul>
					<div class="tab-pane fade show active user-profile__tabs__section" id="activity" role="tabpanel" aria-labelledby="activity-tab">
						<div class="settings">
							<form
								method="post"
								@submit.prevent="updateCareerCategory">
								<div class="settings__field">
									<div class="form-row">
										<div class="form-group col-md-12">
											<div class="settings__value">
												<label for="jobTitle">Name</label>
												<input
													:class="[{'is-invalid': errors.name}]"
													@keydown="clearErrors($event.target.name)"
													v-model="itemData.name"
													name="title"
													value=""
													type="text"
													class="form-control"
													id="jobTitle">
											</div>
											<div class="invalid-feedback">
												{{ errors.name }}
											</div>
										</div>
									</div>
								</div>
								<div class="settings__primary">
									<button
										class="btn btn__save"
										type="button"
										name="button"
										@click.prevent="updateCareerCategory"
										v-bind="{'disabled': loading}">
										Save
									</button>
									<Loader  v-if="loading"/>
								</div>
							</form>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import { updateCareerCategory, getCareerCategory, deleteCareerCategory } from '../../utils/api/users';
import { success, fail } from '../../utils/toast';
import user from '../../utils/validations/user';

export default {
	name: 'Categorydetail',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loadingCategory: true,
			categoryLoaded: false,
			deletingCategory: false,
			loading: false
		};
	},
	components: {
		Loader
	},
	created() {
		this.getCareerCategory();
	},
	methods: {
		deleteCategory() {
			this.deletingCategory = true;
			deleteCareerCategory(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.deletingCategory = false;
						success({
							title: 'Success! Category has been deleted!'
						});
						setTimeout(() => {
							this.$router.go(-1);
						}, 500);
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.deletingCategory = false;
					fail({
						title: `Oops! ${error.response.data.message} || Something went wrong, Please try again`
					});
				});
			return true;
		},
		getCareerCategory() {
			getCareerCategory(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.categoryLoaded = true;
						return true;
					}
					this.categoryLoaded = false;
					return false;
				})
				.catch((error) => {
					this.categoryLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingCategory = false;
				});
			return true;
		},
		updateCareerCategory() {
			const data = { ...this.itemData };
			delete data.created_at;
			delete data.updated_at;
			delete data.id;
			delete data.key;
			delete data.career;
			delete data.segments;
			
			this.validate(data, user.category);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			updateCareerCategory(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({
							title: 'Success! Category has been updated!'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	computed: {
		...mapGetters([
			'getLoggedUser'
		])
	}
};
</script>

<style lang="scss">
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.settings {
	&__field {
		margin-top: 30px;
	}
	&__value {
		padding: 0;
		background: #fff;
		input {
		background: #fafafa;
		border: none;
		font-size: 14px;
		height: 36px;
		}
		label {
		font-weight: bold;
		margin-bottom: 0;
		}
	}
}
</style>
