/**
 * Fetches the a user using the id
 * @returns {Promise} user - a promise of user to be resolved.
 */
export function getUserProfile(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/users/${id}`).then(response => response);
	}
	return Promise.resolve(
		new Response(
			{ message: "Unauthorised" },
			{ status: 403, statusText: "Unauthorised" }
		)
	);
}

/**
 * Edits a user
 * @param {Object} userData - Object of the user information.
 * @returns {Promise} users - a promise of users to be resolved.
 */
export function updateUser(userData) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/users/${userData.id}`, userData);
	}
	return Promise.resolve(
		new Response(
			{ message: "Unauthorised" },
			{ status: 403, statusText: "Unauthorised" }
		)
	);
}

/**
 * Edits a user
 * @param {Object} userData - Object of the user information.
 * @returns {Promise} avatar - a promise of users to be resolved.
 */
export function uploadAvatar(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/users/${id}/avatar`, { file: data });
	}
	return Promise.resolve(
		new Response(
			{ message: "Unauthorised" },
			{ status: 403, statusText: "Unauthorised" }
		)
	);
}

